/**
 * Posts 数据访问层
 * Phase 1: Creator 发帖和 Feed
 */

import { supabase } from "./supabase-client"
import { getCurrentUser } from "./auth"
import { getProfile } from "./profile"

export type Post = {
  id: string
  creator_id: string
  title?: string
  content: string
  media_url?: string
  is_locked: boolean
  created_at: string
  creator?: {
    display_name?: string
    avatar_url?: string
  }
}

/**
 * 创建 post（仅 creator 可创建）
 * @param params Post 参数
 * @returns post.id 或 null（失败）
 */
export async function createPost(params: {
  title?: string
  content: string
  media_url?: string
  is_locked?: boolean
}): Promise<string | null> {
  try {
    // 检查当前用户是否为 creator
    const user = await getCurrentUser()
    if (!user) {
      console.error("[posts] createPost: no user")
      return null
    }

    const profile = await getProfile(user.id)
    if (!profile || profile.role !== "creator") {
      console.error("[posts] createPost: user is not a creator", profile?.role)
      return null
    }

    // 创建 post
    const { data, error } = await supabase
      .from("posts")
      .insert({
        creator_id: user.id,
        title: params.title || null,
        content: params.content,
        media_url: params.media_url || null,
        is_locked: params.is_locked || false,
      })
      .select("id")
      .single()

    if (error) {
      console.error("[posts] createPost error:", error)
      return null
    }

    return data.id
  } catch (err) {
    console.error("[posts] createPost exception:", err)
    return null
  }
}

/**
 * 获取某个 creator 的 posts
 * @param creatorId Creator ID
 * @returns Post 数组
 */
export async function listCreatorPosts(creatorId: string): Promise<Post[]> {
  try {
    const { data, error } = await supabase
      .from("posts")
      .select("*")
      .eq("creator_id", creatorId)
      .order("created_at", { ascending: false })

    if (error) {
      console.error("[posts] listCreatorPosts error:", error)
      return []
    }

    return (data || []).map((post) => ({
      id: post.id,
      creator_id: post.creator_id,
      title: post.title,
      content: post.content,
      media_url: post.media_url,
      is_locked: post.is_locked,
      created_at: post.created_at,
    }))
  } catch (err) {
    console.error("[posts] listCreatorPosts exception:", err)
    return []
  }
}

/**
 * 获取 Feed（最近 N 条 posts，包含 creator 信息）
 * @param limit 限制数量，默认 20
 * @returns Post 数组（包含 creator 信息）
 */
export async function listFeed(limit: number = 20): Promise<Post[]> {
  try {
    // 查询 posts 并 join profiles 获取 creator 信息
    const { data, error } = await supabase
      .from("posts")
      .select(
        `
        id,
        creator_id,
        title,
        content,
        media_url,
        is_locked,
        created_at,
        profiles:creator_id (
          display_name,
          avatar_url
        )
      `
      )
      .order("created_at", { ascending: false })
      .limit(limit)

    if (error) {
      console.error("[posts] listFeed error:", error)
      return []
    }

    return (data || []).map((item: any) => ({
      id: item.id,
      creator_id: item.creator_id,
      title: item.title,
      content: item.content,
      media_url: item.media_url,
      is_locked: item.is_locked,
      created_at: item.created_at,
      creator: {
        display_name: item.profiles?.display_name,
        avatar_url: item.profiles?.avatar_url,
      },
    }))
  } catch (err) {
    console.error("[posts] listFeed exception:", err)
    return []
  }
}

